<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-6"
			:headers="header"
			:items="dataSekolah"
			:items-per-page="10">
	    <template v-slot:item.no="{item}">
	    	{{dataSekolah.map(x => x.id).indexOf(item.id)+1}}    	
	    </template>
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<v-btn icon color="primary">
					<v-icon small>mdi-pencil</v-icon>
				</v-btn>
				<HapusSekolah :id="item.id" />
			</template>		
		</v-data-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'
	import HapusSekolah from './HapusSekolah'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama', value: 'sekolah', sortable: false },
				{ text: 'Provinsi', value: 'provinsi.nama', sortable: false },
				{ text: 'Kabupaten', value: 'kabupaten.nama', sortable: false },
				{ text: 'Jenjang', value: 'jenjang.nama', sortable: false },
				{ text: 'Akreditasi', value: 'akreditasi', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],

			data: [
				{
					name: 's',
					user: 's',
					pass: 's',
					aktivasi: true
				}
			],
		}),

		components: {
			Loader,
			HapusSekolah
		},

		created() {
			this.$store.dispatch('dataSekolah/getDataSekolah')
			this.$store.dispatch('dataSekolah/updateIsLoading', true)
		},

		computed: {
			...mapState('dataSekolah', ['dataSekolah', 'isLoading'])
		}
	}
</script>